import React, { useState, useEffect } from "react";
import { Modal, Button, Form, Image } from "react-bootstrap";

function ProfileSettings({ show, handleClose }) {
  const [currentEmail, setCurrentEmail] = useState("current@example.com"); // Placeholder email
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [isEditingEmail, setIsEditingEmail] = useState(false);
  const [isEditingPassword, setIsEditingPassword] = useState(false);
  const [oldPassword, setOldPassword] = useState("");

  useEffect(() => {
    // Fetch the current email and other profile details from the backend when the component mounts
    async function fetchProfile() {
      try {
        const response = await fetch(
          `${process.env.REACT_APP_API_URL}/getProfile`,
          {
            method: "GET",
            headers: {
              "Content-Type": "application/json",
            },
          }
        );
        const data = await response.json();
        if (response.ok) {
          setCurrentEmail(data.email);
        } else {
          alert(data.message);
        }
      } catch (error) {
        console.error("Error fetching profile:", error);
        alert("An error occurred. Please try again.");
      }
    }

    if (show) {
      fetchProfile();
    }
  }, [show]);

  const handleUpdateEmail = async (e) => {
    e.preventDefault();

    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_URL}/updateEmail`,
        {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ currentEmail, newEmail: email }),
        }
      );

      const data = await response.json();

      if (response.ok) {
        setCurrentEmail(email);
        alert("Email updated successfully");
        setIsEditingEmail(false);
      } else {
        alert(data.message);
      }
    } catch (error) {
      console.error("Error updating email:", error);
      alert("An error occurred. Please try again.");
    }
  };

  const handleUpdatePassword = async (e) => {
    e.preventDefault();
    if (password !== confirmPassword) {
      alert("New passwords do not match");
      return;
    }

    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_URL}/updatePassword`,
        {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            email: currentEmail,
            oldPassword,
            newPassword: password,
          }),
        }
      );

      const data = await response.json();

      if (response.ok) {
        alert("Password updated successfully");
        setIsEditingPassword(false);
      } else {
        alert(data.message);
      }
    } catch (error) {
      console.error("Error updating password:", error);
      alert("An error occurred. Please try again.");
    }
  };

  const handleDeleteAccount = async () => {
    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_URL}/deleteAccount`,
        {
          method: "DELETE",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ email: currentEmail }),
        }
      );

      const data = await response.json();

      if (response.ok) {
        alert("Account deleted successfully");
        handleClose();
      } else {
        alert(data.message);
      }
    } catch (error) {
      console.error("Error deleting account:", error);
      alert("An error occurred. Please try again.");
    }
  };

  return (
    <Modal show={show} onHide={handleClose}>
      <Modal.Header closeButton>
        <Modal.Title>Profile Settings</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Image
          src={`${process.env.PUBLIC_URL}/images/profilepic.png`}
          roundedCircle
          className="mb-3"
        />
        <Form>
          <Form.Group>
            <Form.Label>Email Address</Form.Label>
            {isEditingEmail ? (
              <>
                <Form.Control
                  type="email"
                  placeholder="Enter new email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
                <Button
                  variant="secondary"
                  onClick={() => setIsEditingEmail(false)}
                >
                  Cancel
                </Button>
                <Button variant="primary" onClick={handleUpdateEmail}>
                  Update Email
                </Button>
              </>
            ) : (
              <div>
                <Form.Control type="text" value={currentEmail} readOnly />
                <Button
                  variant="secondary"
                  onClick={() => setIsEditingEmail(true)}
                >
                  Edit
                </Button>
              </div>
            )}
          </Form.Group>
          <Form.Group>
            <Form.Label>Password</Form.Label>
            {isEditingPassword ? (
              <>
                <Form.Control
                  type="password"
                  placeholder="Enter old password"
                  value={oldPassword}
                  onChange={(e) => setOldPassword(e.target.value)}
                />
                <Form.Control
                  type="password"
                  placeholder="Enter new password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
                <Form.Control
                  type="password"
                  placeholder="Confirm new password"
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                />
                <Button
                  variant="secondary"
                  onClick={() => setIsEditingPassword(false)}
                >
                  Cancel
                </Button>
                <Button variant="primary" onClick={handleUpdatePassword}>
                  Update Password
                </Button>
              </>
            ) : (
              <div>
                <Form.Control type="password" value="********" readOnly />
                <Button
                  variant="secondary"
                  onClick={() => setIsEditingPassword(true)}
                >
                  Edit
                </Button>
              </div>
            )}
          </Form.Group>
          <Button variant="danger" onClick={handleDeleteAccount} block>
            Delete Account
          </Button>
        </Form>
      </Modal.Body>
    </Modal>
  );
}

export default ProfileSettings;
